import { useState, useCallback } from 'react';
import useSWR, { mutate } from 'swr';
import {
  Button,
  Container,
  Typography,
  Stack,
  Paper,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  IconButton,
  CircularProgress,
} from '@mui/material';
import { Edit, Delete } from '@mui/icons-material';
import axiosInstance, { endpoints } from 'src/utils/axios';
import ReusableDeletePopover from 'src/components/delete-popup/delete-popup';
import { useSettingsContext } from 'src/components/settings';
import MasterDrawer from 'src/components/drawer';
import CustomerForm from './customerForm';

const fetcher = (url) => axiosInstance.get(url).then((res) => res.data);

export default function OneView() {
  const settings = useSettingsContext();
  const [openDrawer, setOpenDrawer] = useState(false);
  const [editCustomer, setEditCustomer] = useState(null);
  const [deletePopoverEl, setDeletePopoverEl] = useState(null);
  const [selectedCustomerId, setSelectedCustomerId] = useState(null);

  const handleOpenDeletePopover = (event, id) => {
    console.log('ðŸš€ ~ handleOpenDeletePopover ~ id:', id);
    setDeletePopoverEl(event.currentTarget);
    setSelectedCustomerId(id);
  };

  const handleCloseDeletePopover = () => {
    setDeletePopoverEl(null);
    setSelectedCustomerId(null);
  };

  const handleConfirmDelete = () => {
    handleDelete(selectedCustomerId); // your existing delete logic
    handleCloseDeletePopover();
  };

  const { data: customers, error, isLoading } = useSWR(endpoints.customer.list, fetcher);

  const handleAdd = useCallback(() => {
    setEditCustomer(null);
    setOpenDrawer(true);
  }, []);

  console.log({ selectedCustomerId });

  const handleSaveCustomer = useCallback(
    async (data) => {
      try {
        let response;

        if (editCustomer) {
          // Update existing customer
          response = await axiosInstance.put(`/customer/update/${editCustomer._id}`, data);
          console.log(response.data);
          const updatedCustomers = customers.map((customer) =>
            customer._id === editCustomer._id ? response.data : customer
          );
          mutate(endpoints.customer.list, updatedCustomers, false);
        } else {
          // Add new customer
          response = await axiosInstance.post('/customer/add', data);
          const updatedCustomers = [...customers, response.data];
          mutate(endpoints.customer.list, updatedCustomers, false);
        }

        setOpenDrawer(false);
      } catch (err) {
        console.error('Failed to save customer:', err);
        // Optionally show a toast or message here
      }
    },
    [customers, editCustomer]
  );

  const handleDelete = useCallback(
    async (id) => {
      try {
        await axiosInstance.delete(`/customer/delete/${id}`);
        const updatedCustomers = customers.filter((customer) => customer._id !== id);
        console.log("ðŸš€ ~ updatedCustomers:", updatedCustomers);

        mutate(endpoints.customer.list);
      } catch (err) {
        console.error('Failed to save customer:', err);
        // Optionally show a toast or message here
      }
    },
    [customers]
  );

  if (error) {
    return (
      <div style={{ width: '100%', justifyContent: 'center' }}>
        <Stack direction="row" justifyContent="space-between" mb={3}>
          <Typography variant="h4">Customers</Typography>
          <Button variant="contained" onClick={handleAdd}>
            Add Customer
          </Button>
        </Stack>

        <p style={{ color: 'red', fontSize: 20, textAlign: 'center', paddingTop: '150px' }}>
          Failed to load customers!
        </p>
      </div>
    );
  }

  return (
    <Container maxWidth={settings.themeStretch ? false : 'xl'}>
      <Stack direction="row" justifyContent="space-between" mb={3}>
        <Typography variant="h4">Customers</Typography>
        <Button variant="contained" onClick={handleAdd}>
          Add Customer
        </Button>
      </Stack>

      <Paper sx={{ width: '100%', overflow: 'auto' }}>
        {isLoading ? (
          <div style={{ display: 'flex', justifyContent: 'center', padding: '20px' }}>
            <CircularProgress />
          </div>
        ) : (
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Name</TableCell>
                <TableCell>Email</TableCell>
                <TableCell>Number</TableCell>
                <TableCell>Address</TableCell>
                <TableCell align="right">Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {customers && customers.length > 0 ? (
                customers.map((customer) => (
                  <TableRow key={customer._id}>
                    <TableCell>{customer.name}</TableCell>
                    <TableCell>{customer.email || 'N/A'}</TableCell>
                    <TableCell>+91 {customer.number}</TableCell>
                    <TableCell>{customer.address || 'N/A'}</TableCell>
                    <TableCell align="right">
                      <IconButton
                        color="primary"
                        onClick={() => {
                          setEditCustomer(customer);
                          setOpenDrawer(true);
                        }}
                      >
                        <Edit />
                      </IconButton>
                      <IconButton
                        color="error"
                        onClick={(e) => handleOpenDeletePopover(e, customer._id)}
                      >
                        <Delete />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))
              ) : (
                <TableRow>
                  <TableCell colSpan={5} align="center" sx={{ py: 4 }}>
                    <Typography variant="body1" color="text.secondary">
                      No customers found. Head towards <strong>Add Customer</strong> to add them.
                    </Typography>
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        )}
      </Paper>

      <MasterDrawer
        open={openDrawer}
        onClose={() => setOpenDrawer(false)}
        title={editCustomer ? 'Update Customer' : 'Add Customer'}
      >
        <CustomerForm
          initialData={editCustomer || {}}
          onSubmit={handleSaveCustomer}
          onClose={() => setOpenDrawer(false)}
        />
      </MasterDrawer>

      <ReusableDeletePopover
        open={deletePopoverEl}
        onClose={handleCloseDeletePopover}
        onConfirm={handleConfirmDelete}
        title="Delete Customer?"
        description="Are you sure you want to delete this customer?"
      />
    </Container>
  );
}
